@import url('https://fonts.googleapis.com/css2?family=Vazirmatn:wght@500&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: none;
  font-family: 'Vazirmatn', sans-serif;
}

body {
  background-color: #121212;
  color: #ffff;
  margin: 0rem 1rem;
}

#header {
  width: 100%;
  justify-content: center;
  display: flex;
}

#header button {
  margin-top: 2rem;
  margin-bottom: 2rem;
  font-size: 21px;
  display: flex;
  width: 9rem;
  height: 3.5rem;
  justify-content: center;
  align-items: center;
  border-radius: 16px;
  background-color: #181818;
  color: #ba0319;
}

#server {
  display: flex;
  flex-direction: row-reverse;
  width: 100%;
  height: 4rem;
  justify-content: space-between;
  align-items: center;
  background-color: #181818;
  border-radius: 16px;
  padding: 0 1.5rem 0 .75rem;
  margin-bottom: .75rem;
}

.copy-btn {
  width: 4rem;
  height: 2rem;
  background-color: #ba0319;
  border-radius: 12px;
}

.copy-btn:active {
  background-color: #F5685C;
}

#bar {
  width: calc(100% - 4rem);
  background-color: #181818;
  height: 5rem;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
  margin-bottom: 3.5rem;
  display: flex;
  justify-self: center;
  align-self: center;
  justify-content: space-between;
  align-items: center;
  padding: .75rem;
}

.servers-homepage, .education-homepage {
  width: 50%;
  height: 3.5rem;
  border-radius: 16px;
  background-color: #ba0319;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}

.servers-homepage-fill {
  background-color: #181818;
  color: #ffff;
  width: 50%;
  height: 3.5rem;
  border-radius: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}

.education-homepage-fill {
  background-color: #181818;
  color: #ffff;
  width: 50%;
  height: 3.5rem;
  border-radius: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}

